/* logo.module.css */

@keyframes grow {
    0%   {
        clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    -webkit-clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    }

    100% {
        clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    -webkit-clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    }
}
  
.svg {
    /* fill: rgb(29, 151, 252); */
    /* transform-origin: center;
    justify-content: center; */
    animation: grow 2s;
    opacity: 0.4;
    transform: rotate(-90deg);
    display: block;
}
