#container {
  /* overflow: hidden; */
  background-color: whitesmoke;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding-bottom: 1em;
}

#color-one {
  opacity: 0.3;
  width: 70vw;
  height: 60vw;
  position: absolute;
  background-color: green;
  -webkit-filter: blur(100px);
          filter: blur(100px);
  border-radius: 99999px;
  display: inline;
  -webkit-animation: movement infinite 40s;
          animation: movement infinite 40s;
  top: 0;
  left: 40vw;
}

#color-two {
  opacity: 0.2;
  width: 70vw;
  height: 50vw;
  position: absolute;
  display: inline;
  background-color: magenta;
  -webkit-filter: blur(100px);
          filter: blur(100px);
  border-radius: 99999px;
  -webkit-animation: movement2 infinite 50s;
          animation: movement2 infinite 50s;
  top: 0;
  left: 60vw;
}

#color-three {
  opacity: 0.2;
  width: 70vw;
  height: 50vw;
  position: absolute;
  background-color: cyan;
  display: inline;
  -webkit-filter: blur(100px);
          filter: blur(100px);
  border-radius: 99999px;
  -webkit-animation: movement3 infinite 100s;
          animation: movement3 infinite 100s;
  top: 50vh;
  left: 50vw;
}

.svgBox {
  display:inline-block;
  background-color: transparent;
  overflow: visible;
}
