@-webkit-keyframes logo_grow__3waBp {
    0%   {
        clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    -webkit-clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    }

    100% {
        clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    -webkit-clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    }
}

@keyframes logo_grow__3waBp {
    0%   {
        clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    -webkit-clip-path: polygon(50% 10%,50% 0,50% 0,50% 10%);
    }

    100% {
        clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    -webkit-clip-path: polygon(0 100%, 0 0, 100% 0, 100% 100%);
    }
}
  
.logo_svg__1wMBx {
    /* fill: rgb(29, 151, 252); */
    /* transform-origin: center;
    justify-content: center; */
    -webkit-animation: logo_grow__3waBp 2s;
            animation: logo_grow__3waBp 2s;
